.question__wrapper {
    font-weight: 600;
    font-size: 1.3rem;
    margin-top: 30px;

    .input_name_wrapper {
        margin-bottom: 30px;

        input[type="text"] {
            border: 2px solid #75778A;
            border-radius: 10px;
            width: 500px;
            height: 60px;
            padding: 5px 15px;
            font-size: 1.3rem;
            color: #14142B;
            font-weight: 300;
            padding-right: 50px;
        }

        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
            -moz-appearance: textfield;
        }

        p,
        ::placeholder {
            color: #75778A;
            font-weight: 400;
        }

        div {
            margin-top: 10px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }

        div>* {
            margin-right: 30px;
        }

    }

    img {
        outline: 5px solid transparent;
        width: 30px;
        height: 30px;
        margin-left: -75px;
        transition: .2s;
        border-radius: 50%;
        cursor: pointer;
        pointer-events: none;
    }

    img:hover {
        background-color: #dedede;
        outline: 5px solid #dedede;
    }

    .input_select_wrapper {
        margin-top: 30px;

        select {
            margin-top: 20px;
            border: 2px solid #75778A;
            width: 500px;
            height: 60px;
            border-radius: 10px;
            padding: 0 10px;
            font-size: 1.3rem;
            z-index: 1;
        }

        select:focus{
            border: 2px solid #14142B;
            outline: 3px solid #9dbaff;
        }
    }

    .input_checkbox_wrapper {
        margin-top: 20px;

        div {
            display: flex;
            flex-direction: column;

            label {
                width: fit-content;
                display: flex;
                align-items: center;
                margin-top: 15px;
                font-family: 'Montserrat', sans-serif;
                font-size: 1.3rem;
                font-weight: 500;
                cursor: pointer;
            }
        }

        input[type="checkbox"] {
            display: none;
        }

        .fake {
            position: relative;
            border: 2px solid #75778A;
            border-radius: 7px;
            width: 25px;
            height: 25px;
            display: inline-block;
            margin-right: 10px;
        }

        .fake::before {
            content: "";
            border: 1px solid #14142B;
            width: 11px;
            border-radius: 1px;
            background-color: #14142B;
            position: absolute;
            top: 50%;
            left: 62%;
            transform: translate(-50%, -50%) rotate(-45deg);
            display: block;
            opacity: 0;
            transition: .2s;
        }

        .fake::after {
            content: "";
            border: 1px solid #14142B;
            width: 6px;
            border-radius: 1px;
            background-color: #14142B;
            position: absolute;
            top: 58%;
            left: 32%;
            transform: translate(-50%, -50%) rotate(45deg);
            display: block;
            opacity: 0;
            transition: .2s;
        }

        input[type="checkbox"]:checked+.fake::after {
            opacity: 1;
        }

        input[type="checkbox"]:checked+.fake::before {
            opacity: 1;
        }

        input[type="checkbox"]:checked+.fake {
            border-color: #14142B;
        }

    }

    .input_radio_wrapper {
        margin-top: 30px;

        div {
            margin-top: 10px;
        }

        .radio__wrapper {
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }

        span {
            font-family: 'Montserrat', sans-serif;
            font-size: 1.3rem;
            font-weight: 500;
        }
    }

    .input_textarea_wrapper {
        margin-bottom: 30px;

        input {
            border: 2px solid #75778A;
            border-radius: 10px;
            width: 500px;
            height: 60px;
            font-size: 1.3rem;
            color: #75778A;
            font-weight: 300;
        }

        img {
            margin-top: 15px;
        }

        p {
            color: #75778A;
            font-weight: 400;
        }

        ::placeholder {
            color: #D9D9D9;
            font-weight: 400;
            font-size: 1.3rem;
        }

        div {
            margin-top: 20px;
            display: flex;
            justify-content: flex-start;
        }

        div>* {
            margin-right: 30px;
        }

        textarea {
            min-width: 50%;
            max-width: 100%;
            font-size: 1.3rem;
            background-image: url("../../../assets/icons/Expand.svg");
            background-repeat: no-repeat;
            background-position: 100% 100%;
        }

        ::-webkit-resizer {
            display: none;
        }

        .length {
            color: #75778A;
            font-size: 1rem;
        }
    }
}

