.navbar {

    label {
        font-family: 'Montserrat', sans-serif;
        font-size: 1.3rem;
        font-weight: 500;
    }

    .privacy {
        margin-top: 40px;
        
        div {
            display: flex;
            flex-direction: column;

            label {
                display: flex;
                align-items: center;
                font-family: 'Montserrat', sans-serif;
                font-size: 1.3rem;
                font-weight: 500;
                cursor: pointer;
            }
        }

        input[type="checkbox"] {
            display: none;
        }

        .fake {
            position: relative;
            border: 2px solid #75778A;
            border-radius: 7px;
            min-width: 26px;
            min-height: 25px;
            display: inline-block;
            margin-right: 10px;
        }

        .fake::before {
            content: "";
            border: 1.1px solid #14142B;
            width: 11px;
            border-radius: 1px;
            background-color: #14142B;
            position: absolute;
            top: 50%;
            left: 62%;
            transform: translate(-50%, -50%) rotate(-45deg);
            display: block;
            opacity: 0;
            transition: .2s;
        }

        .fake::after {
            content: "";
            border: 1.1px solid #14142B;
            width: 6px;
            border-radius: 1px;
            background-color: #14142B;
            position: absolute;
            top: 58%;
            left: 32%;
            transform: translate(-50%, -50%) rotate(45deg);
            display: block;
            opacity: 0;
            transition: .2s;
        }

        input[type="checkbox"]:checked+.fake::after {
            opacity: 1;
        }

        input[type="checkbox"]:checked+.fake::before {
            opacity: 1;
        }

        input[type="checkbox"]:checked+.fake {
            border-color: #14142B;
        }

        a {
            color: #3F6DE2;
        }

        p {
            font-size: 1.2rem;
            color: red;
        }
    }
}

.navigation {
    margin-top: 50px;
    width: 100%;
    display: flex;

    .navigation__wrapper,
    .prev__btn,
    .next__btn {
        display: flex;
        align-items: center;
        font-size: 1.3rem;
        font-weight: 600;
        text-transform: uppercase;

        img {
            width: 30px;
        }

        .prev__btn,
        .next__btn {
            margin-right: 30px;
            cursor: pointer;
        }

        
        .prev__btn {
            padding: 17px 35px;
            border: 3px solid #3F6DE2;
            border-radius: 25px;
            color: #75778A;
            transition: .2s;
        }
        
        .next__btn {
            padding: 17px 35px;
            border: 3px solid #3F6DE2;
            background-color: #3F6DE2;
            border-radius: 25px;
            color: #fff;
            transition: .2s;
        }

        .prev__btn:hover {
            color: #fff;
            background-color: #3F6DE2;
        }

        .next__btn:hover {
            color: #75778A;
            border: 3px solid #3F6DE2;
            background-color: #fff;
        }

        .prev__btn>*,
        .next__btn>* {
            margin: 0 5px;
        }
    }
}

@media (max-width: 1920px) {

    .navbar {
        label {
            font-size: 1.1rem;
        }

        .privacy {
            margin-bottom: 20px;
        }
    }

    .navigation {

        .navigation__wrapper,
        .prev__btn,
        .next__btn {
            font-size: 1.1rem;

            img {
                width: 30px;
            }
        }
    }
}

@media (max-width: 1550px) {

    .navbar {

        label {
            font-size: .9rem;
        }

        .privacy {
            margin-bottom: 20px;
        }
    }

    .navigation {

        .navigation__wrapper,
        .prev__btn,
        .next__btn {
            font-size: .9rem;

            img {
                width: 30px;
            }
        }

        .prev__btn {
            padding: 13px 35px !important;
        }

        .next__btn {
            padding: 13px 35px !important;
        }
    }
}

@media (max-width: 1280px) {
    .navigation {

        .navigation__wrapper,
        .prev__btn,
        .next__btn {

            img {
                width: 30px;
            }
        }
    }
}

@media (max-width: 991px) {
    .navigation {

        .navigation__wrapper,
        .prev__btn,
        .next__btn {
            font-size: .9rem;

            img {
                width: 25px;
            }

            .prev__btn {
                border-radius: 20px;
            }

            .next__btn {
                border-radius: 20px;
            }
        }
    }
}

@media (max-width: 640px) {
    .navbar {
        padding: 0;
    }

    .navigation {
        margin-top: 20px;
        justify-content: center;

        .navigation__wrapper {
            flex-direction: column;
            align-items: center;
        }

        .navigation__wrapper,
        .prev__btn,
        .next__btn {
            width: 70%;
            justify-content: center;
            font-size: 1rem;

            img {
                width: 25px;
            }

            .prev__btn {
                width: 330px;
                margin-right: 0;
            }

            .next__btn {
                width: 330px;
                margin-top: 15px;
                margin-right: 0;
            }
        }
    }
}

@media (max-width: 360px) {
    .navigation {
        justify-content: center;

        .navigation__wrapper {
            flex-direction: column;
            align-items: center;
        }

        .navigation__wrapper,
        .prev__btn,
        .next__btn {
            width: 100%;
            justify-content: center;
            font-size: .9rem;

            img {
                width: 25px;
            }

            .prev__btn {
                width: 100%;
            }

            .next__btn {
                width: 100%;
                margin-top: 10px;
            }
        }
    }
}